.custom-button {
    min-width: 165px;
    width: auto;
    min-height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 35px 0 35px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-weight: bolder;
    border: none;
    cursor: pointer;
  
    &:hover {
      background-color: white;
      color: black;
      border: 1px solid black;
    }
  }
.Googlebtn{
  background: #4285f4;
  &:hover {
    background-color: #6a97fb;
    color: white;
    border: none;
  }
}